@import 'mixin';
@import 'helpers';

//$app-background: #f8f9fe;
$app-background: #f4f6fb;

body {
    background-color: $app-background;
    background-image: linear-gradient(120deg, #fdfbfb 0%, #ebedee 100%);
}
/* top_navbar */
#top-navbar {
    @import 'navbar';
    @import 'sidebar';
    @import 'bulma-scss/form/_shared';
    @import 'bulma-scss/form/_select';
}
#app-wrapper {
    padding-top: 65px;
    overflow: hidden;
    @import 'bulma-scss/grid/_columns';
    @import 'bulma-scss/components/_level';
    @import 'bulma-scss/form/_shared';
    @import 'bulma-scss/form/_select';
    @import 'bulma-scss/components/_message';
    @import 'cards';
    @import 'breadcrumbs';
    @import 'project';
    @import 'layout';
    @import 'forms';
    @import 'widgets';
    .app-wrapper {
	padding: 60px 20px 0 20px;
	margin-left: 80px;
	min-height: 400px;
	min-height: calc(100vh - 240px);
    }
    > .columns:last-child {
	margin-bottom: 0;
    }
    #footer {
	text-align: right;
	padding-bottom: 50px;
	padding-top: 50px;
	margin-left: 90px;
	margin-right: 30px;
    }
}



@media (max-width: 600px) {
    #top-navbar {
	.main-menu {
	    width: 80px !important;
	}
	.child-menu {
	    left: 80px !important;
	}
	nav.dashboard-nav {
	    margin-left: 80px !important;
	}
	#project_dropdown .select {
	    width: 80px !important;
	}
	.navbar {
	    max-width: calc(100% - 80px) !important;
	    overflow-x: auto;
	}
    }
    .app-wrapper {
	padding-top: 10px !important;
	.card-title {
	    font-size: 1.25rem !important;
	}
	.project-title, .title {
	    font-size: 1.6em !important;
	}
    }
    #app-wrapper > .columns > .column:first-of-type {
	height: 75px;
    }
    #breadcrumbs_go_back {
	top: 105px !important;
    }
    #breadcrumbs {
	left: calc(5% + 80px) !important;
	white-space: nowrap;
    }
}
